// institute-qa

.institute-qa {
	@extend %section-margin-base;
	background-color:#f0f0f0;
	.icon-theme-1 {background-size:cover;background-image: url('data:img/png;base64,');width:rem(360px);height:rem(196px);display:block;}
	.icon-theme-2 {background-size:cover;background-image: url('data:img/png;base64,');width:rem(360px);height:rem(180px);display:block;}
	span {margin:0 auto;}
		.col {
		&:nth-child(1) {
			background:#fff;
			padding:rem(30px);
			border:none !important;
			height:rem(800px);
			.slider-title {
				text-align:left;
				width:100%;
				color:$text-color;
				span.qmark {margin-right:5px;position:relative;top:5px;display:inline-block;background-image:url('data:img/png;base64,');width:26px;height:26px;}
			}
			#institute-icon-slider {
				margin-top:rem(100px);
				.circle-num {
					display:inline-block;
					font-size:20px;
					border:1px solid $institute-text-color;
					width:40px;
					height:40px;
					border-radius:50%;
					display:block;
					color:$institute-text-color;
					font-family:$font-family-eng;
					line-height:1.8;
				}
				.active .circle-num {
					color:#fff;
					background-color: $institute-text-color;
				}
				.owl-dots {
					position:absolute;
					top:rem(220px);
					left:0;right:0;
					margin:0 auto;
					.owl-dot {
						width:40px;
						height:40px;
						background-color:transparent;
					}
				}
				.article {
					text-align:center;
					width:90%;
					padding:rem(100px) 10px rem(50px);
					margin:0 auto;
					label {font-size:rem(28px);font-weight:400;}
					p {font-size:rem(18px);text-align:left;width:90%;margin:0 auto;
		 				.underline_text {font-weight:400;text-align:center;text-decoration:underline;display:block;margin:15px 0 30px;}
					}
				}
			}
		}
		&:nth-child(2) {
			padding:rem(30px);
			background-color:#282832;
			height:rem(800px);
			.slider-title {
				text-align:right;
				width:100%;
				color:#fff;
			}
			.owl-dot.active {opacity:1;}
			.owl-dot {opacity:0.5;}
			#institute-text-slider {
				padding:rem(30px) 0;	
				.text-theme {position:relative;width:rem(240px);height:rem(170px);display:block;margin:0 auto;background-color:#fff;
					p {line-height:1.2;display:block;margin:0 auto;text-align:center;
						.engText {font-family:$font-family-eng;color:$institute-text-color;font-size:rem(140px);}
						.numText {font-family:$font-family-eng;color:$institute-text-color;font-size:rem(100px);}
					}
					.openlink {position:absolute;background-image:url('data:img/png;base64,');width:18px;height:18px;display:block;bottom:rem(10px);right:rem(10px);}
				}
				.owl-nav > * {margin-top:0;}
				.owl-nav {
					position:absolute;
				    width: rem(260px);
				    left: 50%;
				    margin-left:rem(-130px);
				    top: rem(100px);
				    .owl-prev, .owl-next {width:12px;height:20px;display:block;background-color:transparent;}
				    .owl-prev {
				    	background-image:url('data:img/png;base64,');
				    }
				    .owl-next {
				    	background-image:url('data:img/png;base64,');
				    }
				}
			    .owl-dots {
			    	position:absolute;
			    	top:rem(220px);
			    	left:0;right:0;
			    	margin:0 auto;
			    }

				.article {
					text-align:center;
					position:relative;
					height:rem(550px);
					width:80%;
					padding:rem(50px) rem(10%);
					margin:0 auto;
					label {font-size:rem(28px);color:$text-color-gray;text-align:center;margin:0 0 rem(20px);display:inline-block;}
					p {font-size:rem(18px);color:$text-color-gray;text-align:left;}
					.infoDesc {
						&:before {
							content:'';
							background-image:url('data:img/png;base64,');
							width:24px;height:24px;display:block;margin:0 auto 10px;
						}
						position:absolute;
						bottom:rem(50px);left:0;right:0;
						text-align:center;
						span {
							font-size:rem(14px);color:$text-color-gray;
							&:after {
								content: '\00a0\00a0';
							}
						}
					    p {text-align:center;font-size:rem(14px);color:$institute-text-color;font-weight:400;}
					}
				}
			}
		}
	}
}