// institute-overview

.institute-overview {
	@extend %section-margin-base;
    .section-header {margin-top:0;}
    .overview-title {
		.sub-title {
			color:$text-color-gray;
            position:relative;
            display:inline-block;
            width:auto;
            &:before {
            	position:absolute;
                content:'';
                background-color:$institute-text-color;
                height:rem(6px);
                width:100%;
                display:inline-block;
                top:rem(-20px);
            }
        }
	}
	.overview_img {
		margin-top:rem(148px);
	.overview_imgbox {position:relative;background-position:100% 50%;background-image:url('../img/institute/overview-img.jpg');background-repeat:no-repeat;width:rem(508px);height:rem(289px);display:block;}
		.overview_textbox {
			background-color:#282832;
			display:inline-block;
			height:rem(20px);
			padding:rem(20px) 0;
			width:100%;
			font-size:rem(14px);
			color:$text-color-gray;
		}
	}
	.box-btn {
    position:relative;
    height:rem(160px);
    background-color:#282832;
    .box-inner {
        position:relative;
        margin:0 auto;
        padding:rem(40px) rem(80px);
        span {color:#fff;display:block;line-height:1;
            &.title {font-size:rem(28px);font-weight:500;}
            &.sub-title {font-size:rem(14px);}
            &.box-arrow {
                    height:rem(20px);
                    width:rem(326px);
                    display:block;
                    margin:rem(10px) auto;
                    background-image:url('data:img/png;base64,');
            		background-size:cover;
            }
        }
    }
}
	#infoGraphic {
		@extend %section-margin-base;	
		background:$institute-bg-color;
		margin-top:rem(150px);
		padding:50px 0;
		.infoGraphic {
			padding:rem(20px) 0;
			float:left;width:25%;
			span {
				color:$text-color-gray;
				display:block; 
				background-size:100% 100%;
			    -o-background-size: 100% 100%;
			    -webkit-background-size: 100% 100%;
			    background-size:cover;
			    margin:0 auto;
			}
			.countUp {font-family:$font-family-eng;font-size:58px;font-weight:600;display:block;line-height:1;margin-bottom:rem(10px);
				i {font-size:18px;font-family:$font-family;font-style:normal;display:block;}
			}
			.info_desc {line-height:1.4;position:relative;font-size:rem(16px);
				&:before {
					content:'';
					background-color:#fff;
					height:1px;
					width:100%;
					display:inline-block;
					margin-bottom:5px;
				}
				&:after {
				    position: absolute;
				    top: rem(-25px);
				    width:100%;
				    content: '\00b7';
				    text-align: center;
				    font-size: 50px;
				    width: 10px;
				    left: calc(50% - 5px);
				}
			}
			.infoSVG {height:147px;width:100%;position:relative;margin-bottom:rem(15px);
				span {position:absolute;bottom:0px;left:0;right:0;}
			}
			.infoG1 {background-image:url('data:img/png;base64,');width:rem(160px);height:rem(130px);margin-left:rem(30px);}
			.infoG2 {background-image:url('data:img/png;base64,');width:rem(128px);height:rem(126px);margin-left:rem(64px);}
			.infoG3 {background-image:url('data:img/png;base64,');width:rem(193px);height:rem(117px);margin-left:rem(15px);}
			.infoG4 {background-image:url('data:img/png;base64,');width:rem(182px);height:rem(147px);margin-left:rem(25px);}
		}
	}
}